import React, { useState, useEffect } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { useAuth } from "../context/AuthContext";
import axios from "axios";

const ConversationPage = () => {
  const { conversationId } = useParams();
  const { user } = useAuth();
  const navigate = useNavigate();
  
  const [conversation, setConversation] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [messageInput, setMessageInput] = useState("");
  const [sending, setSending] = useState(false);
  
  useEffect(() => {
    // Check if user is authenticated
    if (!user) {
      navigate("/login");
      return;
    }
    
    // Fetch conversation details and check access
    const fetchConversation = async () => {
      try {
        setLoading(true);
        const response = await axios.get(`http://localhost:8000/api/conversations/${conversationId}`);
        
        // Check if user has access to this conversation
        if (response.data.userId !== user.id && !response.data.isPublic) {
          setError("You do not have access to this conversation");
          navigate("/");
          return;
        }
        
        setConversation(response.data);
        setError(null);
      } catch (err) {
        console.error("Error fetching conversation:", err);
        setError("Failed to load conversation");
      } finally {
        setLoading(false);
      }
    };
    
    fetchConversation();
  }, [conversationId, user, navigate]);
  
  const sendMessage = async (e) => {
    e.preventDefault();
    if (!messageInput.trim()) return;
    
    try {
      setSending(true);
      const response = await axios.post(`http://localhost:8000/api/conversations/${conversationId}/messages`, {
        content: messageInput,
        senderId: user.id
      });
      
      // Update the local conversation with the new message
      const newMessage = response.data;
      setConversation(prev => ({
        ...prev,
        messages: [...prev.messages, newMessage]
      }));
      
      // Clear the input
      setMessageInput("");
    } catch (err) {
      console.error("Error sending message:", err);
      setError("Failed to send message");
    } finally {
      setSending(false);
    }
  };
  
  if (loading) {
    return <div className="container mt-5"><p>Loading conversation...</p></div>;
  }
  
  if (error) {
    return <div className="container mt-5"><p className="text-danger">{error}</p></div>;
  }
  
  if (!conversation) {
    return <div className="container mt-5"><p>Conversation not found</p></div>;
  }
  
  return (
    <div className="container mt-4">
      <h2>{conversation.title}</h2>
      <div className="card mt-3">
        <div className="card-body">
          {conversation.messages && conversation.messages.map((message, index) => (
            <div key={index} className={`message ${message.sender === user.id ? "sent" : "received"}`}>
              <p>{message.content}</p>
              <small>{new Date(message.timestamp).toLocaleString()}</small>
            </div>
          ))}
        </div>
      </div>
      
      <div className="mt-4">
        <form onSubmit={sendMessage}>
          <div className="input-group">
            <input
              type="text"
              className="form-control"
              placeholder="Type your message..."
              value={messageInput}
              onChange={(e) => setMessageInput(e.target.value)}
              disabled={sending}
            />
            <button 
              type="submit" 
              className="btn btn-primary" 
              disabled={sending || !messageInput.trim()}
            >
              {sending ? "Sending..." : "Send"}
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default ConversationPage;